import React from "react";
import {Skeleton, Steps} from "antd";

const {Step} = Steps;

class AppealStep extends React.Component {
    renderStep(appeal) {
        if (appeal.state === 1) {
            return (
                <Steps size='small' current={1}>
                    <Step title="" description={appeal.appealTime}/>
                    <Step title="" subTitle={'2'} description=""/>
                    <Step title=""/>
                </Steps>
            )
        } else if (appeal.state === 2) {
            return (
                <Steps size='small' current={2}>
                    <Step title="" description={appeal.appealTime}/>
                    <Step title="" description=""/>
                    {appeal.appealResult === 1 ? <Step title="" description=""/> :
                        <Step status='error' title="" description=""/>}

                </Steps>
            )
        }

    }

    render() {
        return (
            <div style={{}}>
                <Skeleton loading={this.props.loading} active>
                    {this.renderStep(this.props.appeal)}
                </Skeleton>
            </div>
        )
    }
}

export default AppealStep


